@import url(https://fonts.googleapis.com/css?family=Inter:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-bg: #0a0a0a;
  --color-primary: white;
  --color-secondary: #3d3d3d;

  --default-transition: ease-in-out 0.3s;
  --global-side-margin: 80px;

  --carousel-card-w: 290px;
  --carousel-card-w-expanded: 743px;
  --carousel-card-h: 418px;
}

html,
body {
  background-color: #0a0a0a;
  background-color: var(--color-bg);
  color: white;
  color: var(--color-primary);

  font-family: "Inter", sans-serif;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
  padding: 0;
}

a,
a:visited {
  text-decoration: none;
  color: white;
  color: var(--color-primary);
}

header {
  margin: 0 auto;
  padding-top: 53px;
  padding-left: 80px;
  padding-left: var(--global-side-margin);
  padding-right: 80px;
  padding-right: var(--global-side-margin);
}

main {
  margin: 0 auto;
}

footer {
  position: absolute;
  bottom: 8px;
  right: 8px;
}

